import { chargingStations } from "./stations";

$(function () {
    const $map = $("#ymaps");

    $(".js-map-over").on("click", function () {
        $(this).animate({opacity: 0}, 500, function () {
            this.remove()
        })
    })

    if (!$map.length) return;


    ymaps.ready(mapInit);

    function mapInit() {

        const yMap = new ymaps.Map('ymaps', {
            center: [45.028391, 39.043272],
            zoom: 12,
            controls: ['zoomControl', 'typeSelector', 'fullscreenControl', 'trafficControl'],
        });


        chargingStations.forEach(station => {
            const yapPlace = new ymaps.Placemark(station.coords, {
                hintContent: station.name,
                // balloonContent: 'This is a pretty placemark'
            }, {

                iconLayout: 'default#image',
                iconImageHref: './img/content/map-pin.png',
                iconImageSize: [100, 113],
                imageOffset: [-21, -56],
            })

            yMap.geoObjects
                .add(yapPlace)
        })


    }
})