$(function () {
    const $modelCardBtn = $(".js-model-card-btn");

    $modelCardBtn.on("click", function () {
        const that = $(this);
        const { type } = that.data();
        
        that.toggleClass("is-active")
            .siblings().removeClass("is-active")

        that.closest(".js-model-card-panel")
            .next().find(`[data-type="${type}"]`).fadeToggle()
            .siblings().fadeOut(0);
    })
})