$(function() {
    const onlyNumber = (str) => str.toString().replace(/[^0-9]/g, '');
    const $body = $("body")
    const regex = '\\+7 \\([0-6,9]{1}[0-9]{2}\\) [0-9]{3}-[0-9]{2}-[0-9]{2}';
    
    const errorTemplate = (msg) => `
        <div class="form__label-error js-form-label-error">${msg}</div>
    `

    $(".js-input-phone").inputmask({regex})

    $body.on("keyup", ".js-form input[type=\"text\"]", function() {
        const that = $(this);

        $(".js-form-label-error").remove();
        that.closest("label").removeClass("is-error")
    })

    $body.on("submit", ".js-form", function(e) {
        e.preventDefault()
        e.stopPropagation()

        const that = $(this);
        const inputs = that.find("input");
        $(".js-form-label-error").remove();
        checkErrors(inputs)
    })

    function checkErrors(inputs) {
        inputs.each(function(i, item) {
            const $input = $(item);
            const value = $input.val();
            const fieldName = $input.attr("name");
            
            if (value  === "") {
                $input.closest("label").addClass("is-error")
                    .append(errorTemplate("Обязательное поле"))
            } else if (fieldName === "phone" && onlyNumber(value).length < 11) {
                $input.closest("label").addClass("is-error")
                    .append(errorTemplate("Пожалуйста, укажите корректный номер телефона"))
            }
        })

        $("label.is-error").eq(0).focus();
    }
})